<template>
    <div class="m-board">
        <mt-swipe :auto="4000">
            <mt-swipe-item v-for="(item,index) in list ">
                <img :src="item.images.large" alt=""/>
            </mt-swipe-item>
        </mt-swipe>
    </div>
  
</template>
<script>
import Vue from 'vue'
import { Swipe, SwipeItem } from 'mint-ui';
import 'mint-ui/lib/style.css'

Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);

import utilAxios from '../utils/axios'

export default {
    data(){
        return{
            list:[]
        }
    },
    mounted: function () {
      let that = this
      utilAxios.get({
        url: '/Search/searchGoodsPage.htm?keyWords=Coach',
        method: 'get',
        callback: function (res) {
         // that.list = that.list.concat(res.data.subjects)
         console.log(res)
        }
      })
    }
}
</script>


